#main-container {
  max-width: 80%;
  margin: 0 auto;
  font-family: $bodyText;
  color: $bodyTextColor;
}

.data-set-cards {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  align-content: center;
}

.data-card {
  width: 280px;
  height: 280px;
  text-align: center;
  background-color: #F7F7F7;
  border: #E5E5E5 1px solid;
  @include border-radius;
  @include no-select;
  cursor: pointer;
  margin: 3px;
  position: relative;
  p {
    text-align: left;
    position: absolute;
    top: 25px;
    left: 80px;
    font-weight: 600;
  }
}

